import React from 'react';
import { getJwt } from '../../utils/request';
import { Voyager } from 'graphql-voyager';
import styled from 'styled-components';
import './voyager.css';

const introspectionProvider = async (query) => {
  const jwt = await getJwt();
  return fetch(`https://${process.env.REACT_APP_GRAPHQL_PATH}`, {
    method: 'post',
    headers: {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${jwt}`,
    },
    body: JSON.stringify({ query: query }),
  }).then(response => response.json());
}

const VoyagerContainer = styled.div`
  display: flex;
  flex-direction: row;
  overflow: hidden;

  position: relative;
  height: 100%;
`;

const VoyagerComponent = () => {
  return (
    <VoyagerContainer>
      <Voyager
        introspection={introspectionProvider}
        workerURI={process.env.PUBLIC_URL + '/voyager.worker.js'}
      />
    </VoyagerContainer>
  );
};

export default VoyagerComponent;
