import React, { useState, useEffect, useRef } from 'react';
import request from '../utils/request';
import styled from 'styled-components';
import gql from 'graphql-tag';
import _ from 'lodash';
import { useSubscription } from '@apollo/react-hooks';
import { Stage, Layer, Rect, Text, Line, Group, Path } from 'react-konva';
import moment from 'moment';
import { useLocation } from 'react-router-dom';
import { useApiRequest } from '../utils/hooks';

const TEMPLATE_NAMESPACE = 'labs:lauzierj:shopwfloormap';
const defaultTemplate = {
  name: 'Shop Floor',
  namespace: TEMPLATE_NAMESPACE,
  public: true,
}

const savedState = JSON.parse(window.localStorage.getItem('shopfloormap') || '{}');

const BLUE = '#428bca';
const GREEN = '#05A700';
const RED = '#e2062c';
const GRAY = '#555';

const Panel = styled.div`
  display: flex;
  flex-direction: row;
  background-color: #222;
`;

const Map = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
`;

const SideBar = styled.div`
  display: flex;
  flex-direction: column;
  width: 300px;
  background-color: #000;
  overflow-y: scroll;
`;

const RowControls = styled.div`
  position: absolute;
  right: 20px;
  cursor: pointer;
`;

const Row = styled.div`
  position: relative;
  padding: 10px 20px;
  color: #fff;

  &.hidden {
    opacity: .5;
  }

  &.active-status {
    border-left: solid 10px ${GREEN};
  }

  &.idle-status {
    border-left: solid 10px ${BLUE};
  }

  &.alarm-status {
    border-left: solid 10px ${RED};
  }

  &.unavailable-status {
    border-left: solid 10px ${GRAY};
  }
`;

const RowHeading = styled.h1`
  margin: 0;
  font-size: 18px;
  font-weight: normal;
`;

const RowDescription = styled.div`
  padding-top: 5px;
  font-size: 12px;

  span {
    text-transform: capitalize;
  }
`;

const Controls = styled.div`
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  margin: 10px auto;
  background-color: #eee;
  border-radius: 30px;
  padding: 10px 20px;
`;

const Button = styled.button`
  margin: 0 10px;
  padding: 5px 10px;
  border: none;
  background-color: #777;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;

  &:hover {
    background-color: #333;
  }

  &:focus {
    outline: none;
  }

  &.active {
    background-color: ${GREEN};
  }
`;

const EditButton = styled(Button)`
  position: absolute;
  left: 10px;
  bottom: 10px;
`;

const FullScreenButton = styled(Button)`
  position: absolute;
  right: 10px;
  bottom: 10px;
`;

const StatusMap = {
  ACTIVE: {
    score: 0,
    direction: -1,
  },
  UNAVAILABLE: {
    score: Math.pow(10, 10),
    direction: 1,
  },
  IDLE: {
    score: Math.pow(10, 20),
    direction: 1,
  },
};

const Machine = ({ name, initialPosition = {}, machineRef, status, draggable, alarms, onMove }) => {
  let color = status === 'ACTIVE' ? GREEN : BLUE;
  if (status === 'UNAVAILABLE') {
    color = GRAY;
  }
  if (alarms) {
    color = RED;
  }

  const handleDragEnd = (e) => {
    onMove(machineRef, e);
  };

  return (
    <Group x={initialPosition.x || 0} y={initialPosition.y || 0} onDragEnd={handleDragEnd} draggable={draggable}>
      <Path
        x={20}
        y={20}
        data={'M0 0h85L100 15v85h-100z'}
        fill={color}
        shadowBlur={3}
      />
      <Rect
        x={72}
        y={33}
        width={35}
        height={35}
        fill={'rgba(255, 255, 255, .3)'}
      />
      <Text
        verticalAlign={'bottom'}
        lineHeight={1.3}
        fill={'white'}
        x={30}
        y={63}
        width={80}
        height={50}
        wrap
        text={name.trim()}
      />
    </Group>
  )
};

const Home = () => {
  const query = gql`
    subscription {
      currentExecution(args: {}) {
        machine {
          name
          machineRef
        }
        value
        start
      }
    }
  `;

  const alarmQuery = gql`
    subscription {
      currentAlarms(args:{}, where:{value:{_eq:"FAULT"}}) {
        value
        machineRef
        start
        nativeCode
      }
    }
  `;

  const params = new URLSearchParams(useLocation().search);
  const { data, loading, error } = useSubscription(query);
  const { data: alarmData, loading: alarmLoading, error: alarmError } = useSubscription(alarmQuery);
  const [ lines, setLines ] = useState(savedState.lines || []);
  const [ currentLineIndex, setCurrentLineIndex ] = useState(null);
  const stage = useRef(null);
  const panel = useRef(null);
  const [ scale, setScale ] = useState(savedState.scale || 1);
  const [ position, setPosition ] = useState(savedState.position || { x: 0, y: 0 });
  const [ mode, setMode ] = useState('move');
  const [ isManaging, setIsManaging ] = useState(!savedState.visibleMachines || !savedState.visibleMachines.length);
  const [ isEditing, setIsEditing ] = useState(isManaging);
  const [ shift, setShift ] = useState(false);
  const [ prioritizedMachines, setPriotizedMachines ] = useState([]);
  const [ alarms, setAlarms ] = useState({});
  const [ machinePositions, setMachinePositions ] = useState(savedState.machinePositions || {});
  const [ stageDimensions, setStageDimensions ] = useState({ width: 1025, height: 750 });
  const [ visibleMachines, setVisibleMachines ] = useState(savedState.visibleMachines || []);
  const [ visibleCurrentExecutions, setVisibleCurrentExecutions ] = useState([]);
  const [ isFullScreen, setIsFullScreen ] = useState(false);
  const [ activeTemplate, setActiveTemplate ] = useState(defaultTemplate);

  const { data: templatesData, loading: templatesLoading, error: templatesError } = useApiRequest('/templates');
  useEffect(() => {
    if (!templatesData) {
      return;
    }

    const template = _.find(templatesData.templates, { namespace: TEMPLATE_NAMESPACE });
    if (template) {
      setActiveTemplate(template);
    }
  }, [templatesData]);

  useEffect(() => {
    if (!activeTemplate || !activeTemplate.config) {
      return;
    }

    setLines(activeTemplate.config.lines);
    setMachinePositions(activeTemplate.config.machinePositions);
    setPosition(activeTemplate.config.position);
    setScale(activeTemplate.config.scale);
    setVisibleMachines(activeTemplate.config.visibleMachines);
  }, [activeTemplate]);

  useEffect(() => {
    if (!alarmData || !alarmData.currentAlarms) {
      setAlarms({});
      return;
    }

    const tempAlarms = _.groupBy(alarmData.currentAlarms, 'machineRef');
    setAlarms(tempAlarms);
  }, [alarmData]);

  useEffect(() => {
    if (!data || !data.currentExecution) {
      setPriotizedMachines([]);
      return;
    }

    const tempMachines = _(data.currentExecution)
      .map((item) => {
        const status = StatusMap[item.value] || StatusMap.IDLE;
        const alarmMultiplier = alarms[item.machine.machineRef] ? Math.pow(10, 10) : 1;
        const score = (status.score + moment().diff(moment.utc(item.start))) * status.direction * alarmMultiplier;

        return {
          ...item,
          score,
          visible: !!_.includes(visibleMachines, item.machine.machineRef),
        };
      })
      .sortBy('score')
      .reverse()
      .value();

    setPriotizedMachines(tempMachines);
  }, [data, alarms, visibleMachines]);

  useEffect(() => {
    if (!data || !data.currentExecution) {
      setVisibleCurrentExecutions([]);
      return;
    }

    const tempVisibleCurrentExecutions = _.filter(data.currentExecution, (e) => {
      return _.includes(visibleMachines, e.machine.machineRef);
    });

    setVisibleCurrentExecutions(tempVisibleCurrentExecutions);
  }, [data, visibleMachines]);

  const handleManageMachines = () => {
    setIsManaging(!isManaging);
  };

  const handleDraw = () => {
    setMode('draw');
  };

  const handleSave = async () => {
    const configObj = {
      lines,
      scale,
      position: { x: stage.current.x(), y: stage.current.y() },
      machinePositions,
      visibleMachines,
    };

    const bodyObj = {
      ...activeTemplate,
      config: JSON.stringify(configObj),
    };

    const body = JSON.stringify(bodyObj);

    const id = activeTemplate.id;

    const response = id
      ? await request(`${process.env.REACT_APP_API_URL}/templates/${id}`, {
        method: 'PUT',
        body,
      })
      : await request(`${process.env.REACT_APP_API_URL}/templates`, {
        method: 'POST',
        body,
      });

    setActiveTemplate(response.template);

    setIsEditing(false);
    setIsManaging(false);
  };

  const getScaledPointer = () => {
    const pointer = stage.current.getPointerPosition();
    return {
      x: (pointer.x - stage.current.x()) / scale,
      y: (pointer.y - stage.current.y()) / scale,
    };
  };

  const handleMouseDown = (e) => {
    if (mode !== 'draw') {
      return;
    }

    setCurrentLineIndex(lines.length);
    const pointer = getScaledPointer();

    setLines([
      ...lines,
      { start: pointer, end: pointer }
    ]);
  };

  const handleMouseUp = () => {
    setCurrentLineIndex(null);
  };

  const handleMouseMove = (e) => {
    if (currentLineIndex === null) {
      return;
    }

    const line = lines[currentLineIndex];

    const pointer = getScaledPointer();

    const slope = (pointer.y - line.start.y) / (pointer.x - line.start.x);

    const forceHorizontal = shift && Math.abs(slope) >= 1;
    const forceVertical = shift && Math.abs(slope) < 1;

    lines[currentLineIndex] = {
      ...line,
      end: {
        x: forceHorizontal ? line.start.x : pointer.x,
        y: forceVertical ? line.start.y : pointer.y,
      },
    };

    setLines([
      ...lines
    ]);
  };

  const handleRemoveLastLine = () => {
    const newLines = lines.slice(0, lines.length - 1);
    setLines(newLines);
  };

  const handleKeyDown = (e) => {
    setShift(true);
  };

  const handleKeyUp = (e) => {
    setShift(false);
  }

  const handleMouseWheel = (e) => {
    e.evt.preventDefault()
    const pointer = stage.current.getPointerPosition();

    const mousePointTo = {
      x: (pointer.x - stage.current.x()) / scale,
      y: (pointer.y - stage.current.y()) / scale,
    };

    const newScale = e.evt.deltaY > 0 ? scale * 1.05 : scale / 1.05;

    setScale(newScale);
    setPosition({
      x: pointer.x - mousePointTo.x * newScale,
      y: pointer.y - mousePointTo.y * newScale,
    });
  };

  const [ windowSize, setWindowSize ] = useState({ width: window.innerWidth, height: window.innerHeight });
  const handleWindowResize = () => {
    setWindowSize({
      width: window.innerWidth,
      height: window.innerHeight,
    });
  };

  useEffect(() => {
    window.addEventListener('resize', handleWindowResize);
    return () => window.removeEventListener('rezie', handleWindowResize);
  }, [])

  useEffect(() => {
    setStageDimensions({
      width: windowSize.width - 300,
      height: (params.get('embedded') === '1') ? windowSize.height : windowSize.height - 61,
    });
  }, [windowSize]);

  const handleMove = () => {
    setMode('move');
  };

  useEffect(() => {
    document.addEventListener('keydown', handleKeyDown);
    document.addEventListener('keyup', handleKeyUp);

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
      document.removeEventListener('keyup', handleKeyUp);
    };
  });

  const getClassName = (e) => {
    let color = e.value === 'ACTIVE' ? 'active-status' : 'idle-status';
    if (e.value === 'UNAVAILABLE') {
      color = 'unavailable-status';
    }

    if (alarms[e.machine.machineRef]) {
      color = 'alarm-status';
    }

    if (!e.visible) {
      color = `${color} hidden`;
    }

    return color;
  };

  const getStatus = (e) => {
    const machineAlarms = alarms[e.machine.machineRef];

    if (machineAlarms) {
      return _(machineAlarms)
        .sortBy('start')
        .first().nativeCode;
    }

    return e.value.trim();
  };

  const handleClear = () => {
    setLines([]);
    setPosition({ x: 0, y: 0 });
    setScale(1);
    setMachinePositions({});
    setVisibleMachines([]);
    setIsManaging(true);
  };

  const handleMachineMove = (machineRef, e) => {
    setMachinePositions({
      ...machinePositions,
      [machineRef]: { x: e.target.attrs.x, y: e.target.attrs.y },
    })
  };

  const handleFullScreen = () => {
    if (isFullScreen) {
      setIsFullScreen(false);
      if (document.fullscreen) {
        document.exitFullscreen();
      }
    }

    setIsFullScreen(true);
    panel.current.requestFullscreen();
  };

  const handleAddMachine = (ref) => {
    setVisibleMachines([
      ...visibleMachines,
      ref,
    ]);
  };

  const handleRemoveMachine = (ref) => {
    alert('not implemented');
  };

  const handleEdit = () => {
    setIsEditing(true);
  };

  return (
    <>
      <Panel ref={panel} style={{height:`${stageDimensions.height}px`}} onKeyPress={handleKeyDown}>
        <Map>
          <Stage
            draggable={mode==='move'}
            x={position.x}
            y={position.y}
            scaleX={scale}
            scaleY={scale}
            onWheel={handleMouseWheel}
            onContentMousedown={handleMouseDown}
            onContentTouchstart={handleMouseDown}
            onContentMouseup={handleMouseUp}
            onContentTouchend={handleMouseUp}
            onContentMousemove={handleMouseMove}
            onContentTouchmove={handleMouseMove}
            ref={stage}
            width={stageDimensions.width}
            height={stageDimensions.height}>
            <Layer>
              {lines.map((l, index) =>
                <Line
                  key={index}
                  stroke={'#FFE033'}
                  strokeWidth={15}
                  points={[l.start.x, l.start.y, l.end.x, l.end.y]}
                />
              )}
            </Layer>
            <Layer>
              {visibleCurrentExecutions.map((e) =>
                <Machine
                  initialPosition={machinePositions[e.machine.machineRef]}
                  machineRef={e.machine.machineRef}
                  onMove={handleMachineMove}
                  alarms={alarms[e.machine.machineRef]}
                  draggable={mode === 'move' && isEditing}
                  key={e.machine.machineRef}
                  name={e.machine.name}
                  status={e.value}
                />
              )}
            </Layer>
          </Stage>
          {
            isEditing ?
            <Controls>
              <Button className={mode === 'move' && 'active'} onClick={handleMove}>Move</Button>
              <Button className={isManaging && 'active'} onClick={handleManageMachines}>Manage</Button>
              <Button className={mode === 'draw' && 'active'} onClick={handleDraw}>Draw</Button>
              <Button onClick={handleRemoveLastLine}>Undo</Button>
              <Button onClick={handleClear}>Clear</Button>
              <Button onClick={handleSave}>Done</Button>
            </Controls> :
            <EditButton onClick={handleEdit}>Edit</EditButton>
          }
          <FullScreenButton onClick={handleFullScreen}>Full Screen</FullScreenButton>
        </Map>
        <SideBar>
          {prioritizedMachines.filter(e => e.visible || isManaging).map((e) =>
            <Row className={getClassName(e)} key={e.machine.machineRef}>
              {
                (isManaging) &&
                <RowControls>
                  {
                    e.visible ?
                      <span onClick={() => handleRemoveMachine(e.machine.machineRef)}>Remove</span> :
                      <span onClick={() => handleAddMachine(e.machine.machineRef)}>Add</span>
                  }
                </RowControls>
              }
              <RowHeading>{e.machine.name}</RowHeading>
              <RowDescription>{moment.duration(moment().diff(moment.utc(e.start))).humanize()} &middot; <span>{getStatus(e)}</span></RowDescription>
            </Row>
          )}
        </SideBar>
      </Panel>
    </>
  );
};

export default Home;
